@import 'assets/styles/_mixins.scss';

.chat {
    position: fixed;
    right: 2rem;
    bottom: 1.5rem;

    .btn {
        all: unset;
        width: 4rem;
        height: 4rem;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 100%;
        background-color: var(--tertiary-color);
        cursor: pointer;
        transition: .3s;

        &:hover {
            background-color: var(--secondary-color);
        }
        
        .icon {
            font-size: 2.5rem;
            color: #FFFF;
        }
    }

    .content {
        position: fixed;
        bottom: 6.5rem;
        right: 2rem;
        width: 350px;
        border-radius: 4px;
        overflow: hidden;
        box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
        height: 70vh;
        overflow-y: auto;
        background-color: #FFF;
        font-size: .9rem;

        @include sm-max {
            height: 60vh;
        }

        p {
            padding: 1rem;
            text-align: center;
            margin-top: 0;
            margin-bottom: 0;
            background-color: var(--tertiary-color);
            color: #FFF;
        }

        form {
            padding: 1rem;
            display: flex;
            flex-direction: column;
            gap: 1rem;

            .invalid input,
            .invalid textarea {
                border-color: red;
            }

            input,
            textarea {
                margin-top: .2rem;
                transition: .2s;
                box-sizing: border-box;
                padding: .5rem;
                border-radius: 4px;
                border: 1px solid lightgray;
                width: 100%;
            }

            textarea {
                resize: none;
            }

            button {
                all: unset;
                display: block;
                text-align: center;
                padding: .5rem;
                color: #FFF;
                background-color: var(--tertiary-color);
                border-radius: 4px;
            }                
            
        }

        input:focus-within,
        textarea:focus-within {
            outline: initial;
            border: 1px solid var(--tertiary-color);
        }            

        &::-webkit-scrollbar {
            width: 4px;
        }

        &::-webkit-scrollbar-thumb {
            background-color: rgba(211, 211, 211, 0.644);
            border-radius: 4px;
        }
    }

    .successBlock {
        display: flex;
        justify-content: center;
        align-items: center;
        color: var(--tertiary-color);
        height: calc(100% - 92px);
    }

}